<template>
  <div class="login">
        <!-- <mt-field label="昵称" placeholder="请输入昵称" v-model="username"></mt-field> -->
        <!-- <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field> -->
        <div class="enter" v-if="!oldFlag">
            <span class="name">昵称</span>
            <input type="text"  v-model="username" >
            <div class="enter-button">
                <mt-button plain type="primary" @click="jump">登入</mt-button>
            </div>
        </div>
        <div class="enter"  v-else>
            <h2>{{username}},欢迎回来</h2>
            <p><mt-spinner type="triple-bounce"></mt-spinner></p>
        </div>
        <div class="bulr"></div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
        username:'',
        password:'',
        oldFlag:false,
    }
  },
  methods:{
    jump(){
        localStorage.setItem('username',this.username);
        this.$router.push('/index')
    }
  },
  created(){
        this.oldFlag=localStorage.getItem('username')?true:false;
        this.username=localStorage.getItem('username')?localStorage.getItem('username'):'';
  },
  mounted(){
      if(this.oldFlag){
          setTimeout(this.jump,2000)
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    @import 'login.scss';
</style>



