import Vue from 'vue'
import router from '../router';
import axios from 'axios'
import AxiosConfig from './axios-config'
import VueAxios from 'vue-axios'
import wx from 'weixin-js-sdk';
import qs from 'qs';
import wxConfig from './wx-config'
import $ from 'jquery'

new Vue({
  router
});

let AxiosSND = axios.create(AxiosConfig);
// axios拦截器 判断超时
AxiosSND.interceptors.response.use((res) => {
  return res;
}, (error) => {
  if (error.response) {
    return Promise.resolve(error.response);
  } else if (error.request) {
    console.error('网络异常，请检查您的网络并刷新重试！')
    return Promise.reject(error.request);
  } else {
    console.error('未知错误，请刷新重试！')
    return Promise.reject(error.message);
  }
});
Object.defineProperties(Vue.prototype, {
  axios: {
    get() {
      return AxiosSND
    }
  },
  $http: {
    get() {
      return AxiosSND
    }
  },
  videojs: {
    get() {
      return videojs
    }
  },
  wx: {
    get() {
      return wx
    }
  }
});

let toolkit = {
  // 深度clone一个对象
  cloneObj: function (obj) {
    let str, newobj = obj.constructor === Array ? [] : {};
    if (typeof obj !== 'object') {
      return;
    } else if (window.JSON) {
      str = JSON.stringify(obj), //系列化对象
        newobj = JSON.parse(str); //还原
    } else {
      for (let i in obj) {
        newobj[i] = typeof obj[i] === 'object' ?
          cloneObj(obj[i]) : obj[i];
      }
    }
    return newobj;
  },
  // 在location中获得指定参数的值，注意传入参数名称要和location中的参数名称相同
  GetQueryUrl: (url, name) => {
    let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
    let r = ('?' + url.split('?')[1]).substr(1).match(reg);
    if (r != null) {
      return unescape(r[2]);
    } else {
      return "";
    }
  },
  //时间格式化
  getTime: function (time, formatType) {
    var newDate = new Date();
    newDate.setTime(time * 1000);
    Date.prototype.format = function (format) {
      var date = {
        "M+": this.getMonth() + 1,
        "d+": this.getDate(),
        "h+": this.getHours(),
        "m+": this.getMinutes()
      };
      if (/(y+)/i.test(format)) {
        format = format.replace(RegExp.$1, (this.getFullYear() + '').substr(4 -
          RegExp.$1.length));
      }
      for (var k in date) {
        if (new RegExp("(" + k + ")").test(format)) {
          format = format.replace(RegExp.$1, RegExp.$1.length == 1 ? date[k] : (
            "00" + date[k]).substr(("" + date[k]).length));
        }
      }
      return format;
    }
    return newDate.format(formatType);
  },
  // 时间特殊格式
  timeFormat: (dateStr) => {
    var publishTime = dateStr,
      d_seconds,
      d_minutes,
      d_hours,
      d_days,
      timeNow = parseInt(new Date().getTime() / 1000),
      d,

      date = new Date(publishTime * 1000),
      Y = date.getFullYear(),
      M = date.getMonth() + 1,
      D = date.getDate(),
      H = date.getHours(),
      m = date.getMinutes(),
      s = date.getSeconds();
    //小于10的在前面补0
    if (M < 10) {
      M = '0' + M;
    }
    if (D < 10) {
      D = '0' + D;
    }
    if (H < 10) {
      H = '0' + H;
    }
    if (m < 10) {
      m = '0' + m;
    }
    if (s < 10) {
      s = '0' + s;
    }

    d = timeNow - publishTime;
    d_days = parseInt(d / 86400);
    d_hours = parseInt(d / 3600);
    d_minutes = parseInt(d / 60);
    d_seconds = parseInt(d);

    if (d_days > 0 && d_days < 3) {
      return d_days + '天前';
    } else if (d_days <= 0 && d_hours > 0) {
      return d_hours + '小时前';
    } else if (d_hours <= 0 && d_minutes > 0) {
      return d_minutes + '分钟前';
    } else if (d_seconds < 60) {
      if (d_seconds <= 0) {
        return '刚刚发表';
      } else {
        return d_seconds + '秒前';
      }
    } else if (d_days >= 3 && d_days < 30) {
      return M + '-' + D + ' ' + H + ':' + m;
    } else if (d_days >= 30) {
      return Y + '-' + M + '-' + D + ' ' + H + ':' + m;
    }
  },
  // 获得数据
  getData: (type, url, params) => {
    let axios;
    if ('get' == type) {
      axios = AxiosSND.get(url, {
        params: params
      })
    } else if ('post' == type) {
      axios = AxiosSND.post(url, qs.stringify(params));
    }
    let promise = new Promise((resolve, reject) => {
      axios.then(response => {
        if (200 == response.status) {
          if (401 == response.data.code) {
            console.error('未登录')
          } else if (500 == response.data.code) {
            console.error('接口出错了！url:' + url)
          } else if (404 == response.data.code || 405 == response.data.code) {
            console.error('接口未找到!url:' + url)
          } else if (403 == response.data.code) {
            console.error('token不存在')
          } else if (422 == response.data.code) {
            console.error('参数缺失，请确认参数')
          } else {
            console.error('other',response);
          }
          resolve(response);
        }
      }).catch(error => {
        if (error.response) {
          resolve(error.response);
          console.log(error.response, 'error response');
        } else if (error.request) {
          resolve(error.request);
          console.log(error.request, 'error request');
        } else {
          resolve(error.message);
          console.log(error.message, 'error message');
        }
      })
    });
    return promise;
  },
  // 分享函数
  shareFuc: (shareData) => {
    let jsApiList = [
      'onMenuShareAppMessage',
      'onMenuShareTimeline',
      'onMenuShareQQ',
      'onMenuShareWeibo',
      'onMenuShareQZone',
    ];
    wxConfig.wxConfig(jsApiList);
    wx.ready(() => {
      wx.onMenuShareAppMessage(shareData);
      wx.onMenuShareTimeline(shareData);
      wx.onMenuShareQQ(shareData);
      wx.onMenuShareWeibo(shareData);
      wx.onMenuShareQZone(shareData);
    })
  },
  // 默认的分享
  defaultShare: (url) => {
    let shareData = {
      title: '聊天室小demo', // 分享标题
      desc: '马栏山专用聊天室', // 分享描述
      link: encodeURI('http://119.23.57.251:3000/'), // 分享链接
      imgUrl: 'http://yylm-wd-wap.qeebu.com/static/shareLama.jpg'
    };
    toolkit.shareFuc(shareData);
  },
  // hidden
  hideShare: () => {
    let jsApiList = [
      'hideMenuItems',
      'hideOptionMenu'
    ];
    wxConfig.wxConfig(jsApiList);
    wx.ready(() => {
      wx.hideOptionMenu();
      wx.hideMenuItems({
        menuList: [
          "menuItem:share:appMessage",
          "menuItem:share:timeline"
        ]
      });
    })
  },
  // 将时间戳转换为时间节点，1分钟前，1小时前...
  timeStamp2TimeFrame(timeStamp) {
    var minute = 1000 * 60;  //分钟
    var hour = minute * 60; //小时
    var day = hour * 24;  //天
    var month = day * 30;  //月
    var now = new Date().getTime();  //当期时间
    if (timeStamp.length !== 13) {
      timeStamp *= 1000;
    }
    var timeInterval = now - parseInt(timeStamp);  //时间间隔
    var result = '';
    if (timeInterval >= 0) {
      var monthC = timeInterval / month;
      var weekC = timeInterval / (7 * day);
      var dayC = timeInterval / day;
      var hourC = timeInterval / hour;
      var minC = timeInterval / minute;
      if (dayC >= 1 && dayC <= 2) {
        result = "昨天";
      } else if (dayC < 1 && hourC >= 1) {
        result = parseInt(hourC) + "小时前";
      } else if (hourC < 1 && minC >= 1) {
        result = parseInt(minC) + "分钟前";
      } else if (minC < 1) {
        result = "刚刚";
      } else if (monthC >= 12) {
        var s = new Date(timeStamp);
        result = s.getFullYear() + '年' + (s.getMonth() + 1) + '月' + s.getDate() + '日';
      } else {
        var s = new Date(timeStamp);
        result = (s.getMonth() + 1) + '月' + s.getDate() + '日';
      }
    } else {
      result = ''
    }
    return result;
  },
  timeStamp2Standard(timeStamp) {
    if (timeStamp.length !== 13) {
      timeStamp *= 1000;
    }
    var date = new Date(timeStamp);
    return date.getFullYear() + '-' + (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-' + date.getDate() + ' ' + date.getHours() + ':' + (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes());
  },
  timeStamp2Standard2(timeStamp) {
    if (timeStamp.length !== 13) {
      timeStamp *= 1000;
    }
    var date = new Date(timeStamp);
    return date.getFullYear() + '-' + (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-' + date.getDate();
  },
  // 修复Android小键盘和按钮冲突的问题，要在mounted以后的周期中使用
  resizeWindow(clientHeight, id = 'btn_next') {
    id = `#${id}`;
    window.onresize = () => {
      if (clientHeight > document.documentElement.clientHeight) {
        $(id).hide()
      } else {
        $(id).show()
      }
    }
  },
};

export default toolkit


